  pointer-events: none; /* Disables mouse events */
  opacity: 0.5; /* Optional: makes it look disabled */
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5px;
  background-color: #ffffff; /*Background color of the container*/
  border-radius: 8px; /* Optional: rounded corners */
  padding-bottom: 40px;
}

.tabtext {
  color: black;
}

.startButton {
  background-color: #28a745; /* Green background color */
  color: white; /* Text color */
  border: none; /* Remove default border */
  border-radius: 12px; /* Rounded corners */
  padding: 10px 20px; /* Padding inside the button */
  font-size: 16px; /* Font size */
  cursor: pointer; /* Pointer cursor on hover */
  width: 200px;
  /*box-shadow: 0 4px 8px rgba(40, 167, 69, 0.3);  Drop shadow with a lighter green shade */
  transition: background-color 0.3s, box-shadow 0.3s; /* Smooth transition for hover effects */
}

/* Button hover state */
.button:hover {
  background-color: #34c759; /* Light green background color on hover */
  box-shadow: 0 4px 12px rgba(40, 167, 69, 0.5); /* Slightly more intense shadow on hover */
}

.stopButton {
  background-color: #521212; /* Green background color */
  color: white; /* Text color */
  border: none; /* Remove default border */
  border-radius: 12px; /* Rounded corners */
  padding: 10px 20px; /* Padding inside the button */
  font-size: 16px; /* Font size */
  width: 200px;
  cursor: pointer; /* Pointer cursor on hover */
  box-shadow: 0 4px 8px rgba(214, 77, 81, 0.3); /* Drop shadow with a lighter green shade */
  transition: background-color 0.3s, box-shadow 0.3s; /* Smooth transition for hover effects */
}

.form-check-label {
  display: inline-block;
  margin-bottom: 0;
  vertical-align: middle;
  cursor: pointer;
}

.input-group {
  position: relative;
  display: flex; /* Ensures child elements are aligned in a row */
  flex-wrap: nowrap; /* Prevents wrapping; keeps elements on the same row */
  align-items: center; /* Vertically centers items */
  height: 2.5rem;
}

.input-group-text {
  display: flex; /* Allows child items to be aligned within this element */
  align-items: center; /* Centers child items vertically */
  padding: 0.375rem 0.75rem; /*Adjusts padding */
  margin-bottom: 0; /* Removes bottom margin */
  font-size: 1rem; /* Font size */
  font-weight: 400; /* Font weight */
  line-height: 1.5; /* Line height */
  color: #495057; /* Text color */
  background-color: #e9ecef; /* Background color */
  border: 1px solid #ced4da; /* Border */
  border-radius: 0.375rem; /* Border radius */
  height: 2.5rem; /* Set a fixed height */
  width: 300px;
  /* Adjust width or flex properties if needed to fit your design */
}

.input-group input {
  flex: 1; /* Allows input to take remaining space */
  height: 2.5rem; /* Ensure the input field height matches the .input-group-text height */
  /* padding: 0.375rem 0.75rem; Ensure padding matches .input-group-text */
  border: 1px solid #ced4da; /* Match the border style of .input-group-text */
  /* border-radius: 0.375rem; Match border radius of .input-group-text */
}

/* Optional: Adjust focus and hover states */
.input-group input:focus,
.input-group input:hover {
  border-color: #80bdff; /* Example focus border color */
  outline: none; /* Removes default outline */
}

.mb-3 {
  margin-bottom: 1rem; /* Standard margin size for spacing */
}

.form-control {
  display: block;
  width: 100%;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.form-select {
  display: block;
  /* width: 100%; */
  height: calc(2.25rem + 2px);
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  background-color: #429ede;
  color: #ffffff;
  font-weight: bold;
  background-clip: padding-box;
  border: 1px solid #a5a5a4;
  margin-top: 0;
  margin-bottom: 0;
  appearance: none; /* Remove default styling in some browsers */
}

.nav-tabs {
  list-style-type: none; /* Removes bullet points */
  padding-left: 0; /* Removes default padding */
  margin-bottom: 0; /* Removes bottom margin */
  display: flex; /* Aligns items in a row */
}

/* Ensure each tab item (usually a <li>) does not have default margins and padding */
.nav-tabs .nav-item {
  margin-bottom: -1px; /* Removes the default margin between tab items */
}

/* Ensure the individual tab links are displayed as block-level elements */
.nav-tabs .nav-link {
  display: block; /* Makes each tab link a block element */
  padding: 0.5rem 1rem; /* Adjust padding as needed */
  color: black;
  margin: 0; /* Removes margin */
  border: 1px solid transparent; /* Sets border style */
  border-radius: 0.375rem 0.375rem 0 0; /* Adjust border radius as needed */
}

/* Style for the active tab */
.nav-tabs .nav-link.active {
  color: #284f93; /* Text color for active tab */
  font-weight: bold;
  background-color: #fff; /* Background color for active tab */
  border-color: #f7901e #e6e4de #fff;
  border-top-width: medium; /*Border color for active tab*/
}

/* Style for disabled tab */
.nav-tabs .nav-link.disabled {
  color: #6c757d; /* Text color for disabled tab */
  pointer-events: none; /* Disables interactions */
}

.alert {
  position: relative;
  padding: 0.75rem 1.25rem;
  margin-bottom: 1rem;
  border: 1px solid transparent;
  border-radius: 0.375rem;
}

.alert-heading {
  font-size: 1.25rem;
  font-weight: 500;
}

.alert-link {
  font-weight: 500;
}

.alert-dismissible {
  padding-right: 4.5rem;
}

.alert-dismissible .btn-close {
  position: absolute;
  top: 0.5rem;
  right: 1rem;
  z-index: 2;
}

.alert-danger {
  color: #721c24;
  background-color: #f8d7da;
  border-color: #721c24;
  margin-top: 2%;
}

.alert-danger hr {
  border-top-color: #f1b0b7;
}

.spinner-grow {
  display: inline-block;
  width: 2rem;
  height: 2rem;
  vertical-align: text-bottom;
  border: 0.25em solid transparent;
  border-radius: 50%;
  border-top-color: currentColor;
  animation: spinner-grow 0.75s linear infinite;
}

@keyframes spinner-grow {
  0% {
    transform: scale(0.1);
    opacity: 1;
  }
  50% {
    transform: scale(1);
    opacity: 0.5;
  }
  100% {
    transform: scale(0.1);
    opacity: 0;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.form-check-label {
  display: inline-block; /* Makes the label behave like an inline-block element */
  margin-bottom: 0; /* Removes bottom margin */
  font-weight: 400; /* Normal font weight */
  line-height: 1.5; /* Adjusts line height for better readability */
  cursor: pointer; /* Pointer cursor on hover for better UX */
}

/* -------------------------- TERMINAL DESIGN ---------------------------------- */

/* Terminal Container */
.terminal-container {
  margin-top: 5%;
  width: 70%;
  height: 60vh;
  background-color: #0c0c0c; /* Dark background similar to Windows Terminal */
  color: #ffffff; /* Default text color */
  padding: 10px;
  display: flex;
  flex-direction: column;
}

/* Terminal Header (Tabs) */
.terminal-header {
  background-color: #1e1e1e; /* Slightly lighter than the main background */
  color: #cccccc;
  padding: 5px 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #3a3a3a; /* Subtle divider line */
}

/* Tabs */
.terminal-tab {
  padding: 5px 15px;
  margin-right: 5px;
  background-color: #333333; /* Inactive tab color */
  color: #cccccc;
  cursor: pointer;
  border-radius: 5px 5px 0 0;
}

.terminal-tab.active {
  background-color: #0c0c0c; /* Active tab color matches terminal background */
  color: #ffffff; /* Active tab text color */
  border-bottom: 2px solid #007acc; /* Accent color for the active tab */
}

/* Terminal Content Area */
.terminal-content {
  flex: 1;
  background-color: #0c0c0c; /* Match the dark terminal background */
  color: #34c759;
  white-space: pre-wrap;
  overflow-y: auto;
  padding: 10px;
  font-size: 14px;
  line-height: 1.5;
  font-family: Consolas, "Courier New", monospace; /* Matches Windows Terminal font style */
}

/* Command Line */
.terminal-input {
  color: #34c759; /* Bright green color for input text */
  background-color: #0c0c0c;
  outline: none;
  border: none;
  /* background: transparent; */
  width: 100%;
  font-family: Consolas, "Courier New", monospace; /* Matches Windows Terminal font style */
}

/* Scrollbar Styles */
.terminal-content::-webkit-scrollbar {
  width: 8px;
}

.terminal-content::-webkit-scrollbar-thumb {
  background-color: #3a3a3a; /* Custom scrollbar color */
  border-radius: 4px;
}

.terminal-content::-webkit-scrollbar-track {
  background: #1e1e1e; /* Scrollbar track color */
}
